import React from 'react';
import { motion } from 'framer-motion';
import { cn } from '../../utils';
import { Button } from '../Button';

/**
 * Header Component
 * Reusable header for screens with title and subtitle
 */
export const Header = ({
  title,
  subtitle,
  icon,
  className = '',
  titleClassName = '',
  subtitleClassName = '',
  showBackButton = false,
  onBack = null,
  ...props
}) => {
  return (
    <motion.div
      className={cn('mb-8', className)}
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      {...props}
    >
      {/* Back button ở trên cùng */}
      {showBackButton && onBack && (
        <div className="mb-4">
          <Button
            onClick={onBack}
            variant="outline"
            size="sm"
            className="text-sm"
          >
            ← Quay lại
          </Button>
        </div>
      )}

      <div className="text-center">
        {icon && (
          <motion.div
            className="text-6xl mb-4 inline-block"
            animate={{
              scale: [1, 1.1, 1],
              rotate: [0, 5, -5, 0],
            }}
            transition={{
              duration: 2,
              repeat: Infinity,
              repeatDelay: 3,
            }}
          >
            {icon}
          </motion.div>
        )}
        <h1 className={cn(
          'text-5xl font-bold mb-2 bg-gradient-to-r from-primary-600 to-secondary-600 bg-clip-text text-transparent',
          'drop-shadow-sm',
          titleClassName
        )}>
          {title}
        </h1>
        {subtitle && (
          <p className={cn('text-xl text-gray-600 font-medium', subtitleClassName)}>
            {subtitle}
          </p>
        )}
      </div>
    </motion.div>
  );
};

